:root{
  --color-body: #0e2d02;
  --color-subtitle: #559951;
  --color-title: #185004;
  --color-background: #14e441;
}

.navbar{
  background: var(--color-background);
  color: #fff;
  display: flex;
}

.navbar-content{
  display: flex;
  list-style: none;
  align-items: center;
  margin-left: auto;
}

.navbar-content a{
  color: #fff;
  text-decoration: none;
}

.navbar-content a:hover{
  text-decoration: underline;
}

.navbar-content li{
  padding-right: 50px;
  font-size: 1.1rem;
}

.navbar-logo{
  padding: 0px 20px;
}

.navbar-toggle{
  display: none;
  border: none;
}

.navbar-toggle svg{
  width: 40px;
  height: 40px;
}


.navbar-mobile{
  display: none;
}


@media screen and (max-width: 540px){
  .navbar-mobile{
    display: block;
    background-color: var(--color-background);
    margin: 0;
    color: #fff;
    list-style: none;
    padding-left: 20px;
  }
  .navbar-mobile li{
    font-size: 1.3rem;
    border-bottom: 1px solid var(--color-subtitle);
    padding: 5px;
    margin-right: 10px;
  }
  .navbar-content{
    display: none;
  }

  .navbar-content{
    flex-direction: column;
  }
  
  .navbar-toggle{
    display: block;
    margin-left: auto ;
    background-color: transparent;
    padding: 10px;
    position: absolute;
    top: 0;
    right: 0;
  }
}


.hero{
  background-image: url(./Images/waves-bottom.svg);
  background-repeat: repeat-x;
  height: 550px;
}

#filler{
  background-color: var(--color-background);
  height: 120px;
}

.hero-title{
  font-size: 5rem;
  color: #fff;
  text-align: center;
  padding: 0;
  margin: 0;
  padding-top: 60px;
  margin-bottom: 50px;
  position: relative;
  top: -130px;
}

.hero-btn{
  position: relative;
  top: -130px;
}

@media screen and (max-width: 700px){
  .hero-title{
    font-size: 3rem;
  }

  .hero-btn{
    font-size: 0.6rem;
  }
}

.bold{
  font-weight: bolder;
}

.btn{
  padding: 10px;
  font-size: 1.5rem;
  text-transform: uppercase;
  text-align: center;
  display: block;
  margin: auto;
  max-width: 200px;
  border-radius: 30px;
  transition: 0.3s;
  text-decoration: none;
  user-select: none;
}

.btn-back{
  margin-bottom: 20px;
  max-width: 300px;
}

.buttons{
  display: flex;
  flex-direction: row;
  position: relative;
  top: 20px;
}

@media screen and (max-width: 453px){
  .buttons{
    flex-direction: column;
  }

  .btn-back{
    width: 80%;
  }
}

@media screen and (max-width: 504px) {
  
}

.clear{
  border: 2px solid #fff;
  color: #fff;
}

.clear:hover{
  background-color: #fff;
  color: var(--color-background);
  transition: 0.3s;
}

.full{
  background-color: #fff;
  color: var(--color-background);
}

.full:hover{
  border: 2px solid #fff;
  background-color: var(--color-background);
  color: #fff;
  transition: 0.3s;
}

.explain-section{
  padding-top: 40px;
  display: inline-grid;
  grid-template-columns: 1fr 1fr;
  width: 100%;
}

.explain-img-no-border{
  width: 70%;
  display: block;
  margin: auto;
 /*border: 2px solid var(--color-background);*/
  border-radius: 40px; 
  padding: 10px;
}

.explain-content{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.explain-title{
  color: var(--color-title);
  font-size: 4rem;
  margin: 0;
}

.explain-subtitle{
  font-size: 3rem;
  color: var(--color-subtitle);
  margin: 0;
}

.explain-img{
  width: 60%;
  display: block;
  margin: auto;
  border: 2px solid var(--color-background);
  border-radius: 40px;
  padding: 30px;
}

@media screen and (max-width: 1000px){
  .explain-title{
    font-size: 2.5rem;
  }

  .explain-section{
    font-size: 1rem;
  }
}

.grid{
  display: grid;
}

.grid--1x2{
  grid-template-columns: 1fr 1fr;
}


@media screen and (max-width: 600px){
  .grid--1x2{
    grid-template-columns: 1fr;
  }

  .explain-subtitle{
    padding-bottom: 40px;
  }
}

.statement-container{
