.toggle-mode{
  width: 50px;
  padding: 30px;
  /* border-radius: 100px; */
  cursor: pointer;
}

.toggle-container{
  display: flex;
  flex-direction: row;
  align-items: center;
  border-radius: 20px;
  width: 200px;
  height: 70px;
  position: relative;
  top: 5px;
  transition: 0.3s;
  cursor: pointer;
  user-select: none;
}

.toggle-container-dark:hover{
  background-color: #fff;
  color: #000;
  transition: 0.3s;
  border: 1px solid #fff;
}

.toggle-container-light{
  border: 1px solid #000;
}

.toggle-container-light:hover{
  background-color: black;
  color: #fff;
  transition: 0.3s;
}

.toggle-container-light p {
  color: #fff;
  font-size: 1.5rem;
}
.toggle-container-dark p {
  color: #000;
  font-size: 1.5rem;
}

.toggle-dark{
  width: 50px;
}

